function slider({container, slide, nextArrow, prewArr, totalCounter, curretCounter, wrapper, field}) {
	
	//Ivan`s slider

	const slides = document.querySelectorAll(slide),
		slider = document.querySelector(container),
		prev = document.querySelector(prewArr),
		next = document.querySelector(nextArrow),
		total = document.querySelector(totalCounter),
		current = document.querySelector(curretCounter),
		slidesWrapper = document.querySelector('.offer__slider-wrapper'),
		slidesField = document.querySelector('.offer__slider-inner'),
		width = window.getComputedStyle(slidesWrapper).width;

	let slideIndex = 1;
	let offset = 0;

	if (slides.length < 10) {
		total.textContent = `0${slides.length}`;
		current.textContent = `0${slideIndex}`;
	} else {
		total.textContent = slides.length;
		current.textContent = slideIndex;
	}

	slidesField.style.width = 100 * slides.length + '%';
	slidesField.style.display = 'flex';
	slidesField.style.transition = '0.5s all';

	slidesWrapper.style.overflow = 'hidden';
	slides.forEach(slide => {
		slide.style.width = width;
	});

	slider.style.position = 'relative';

	const indicators = document.createElement('ol'),
		dots = [];
	indicators.classList.add('carousel-indicators');
	indicators.style.cssText = `
        position: absolute;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 15;
        display: flex;
        justify-content: center;
        margin-right: 15%;
        margin-left: 15%;
        list-style: none;
    `;
	slider.append(indicators);

	for (let i = 0; i < slides.length; i++) {
		const dot = document.createElement('li');
		dot.setAttribute('data-slide-to', i + 1);
		dot.style.cssText = `
            box-sizing: content-box;
            flex: 0 1 auto;
            width: 30px;
            height: 6px;
            margin-right: 3px;
            margin-left: 3px;
            cursor: pointer;
            background-color: #fff;
            background-clip: padding-box;
            border-top: 10px solid transparent;
            border-bottom: 10px solid transparent;
            opacity: .5;
            transition: opacity .6s ease;
        `;
		if (i == 0) {
			dot.style.opacity = 1;
		}
		indicators.append(dot);
		dots.push(dot);
	}

	function deleteNotDigits(str) {
		return +str.replace(/\D/g, '');
	}

	function setOpacity(elements) {
		elements.forEach(elem => elem.style.opacity = '.5');
		elements[slideIndex - 1].style.opacity = 1;
	}

	next.addEventListener('click', () => {
		if (offset == deleteNotDigits(width) * (slides.length - 1)) {
			offset = 0;
		} else {
			offset += deleteNotDigits(width);
		}
		slidesField.style.transform = `translateX(-${offset}px)`;

		if (slideIndex == slides.length) {
			slideIndex = 1;
		} else {
			slideIndex++;
		}

		if (slides.length < 10) {
			current.textContent = `0${slideIndex}`;
		} else {
			current.textContent = slideIndex;
		}

		setOpacity(dots);
	});

	prev.addEventListener('click', () => {
		if (offset == 0) {
			offset = +width.deleteNotDigits(width) * (slides.length - 1);
		} else {
			offset -= +width.deleteNotDigits(width);
		}

		slidesField.style.transform = `translateX(-${offset}px)`;

		if (slideIndex == 1) {
			slideIndex = slides.length;
		} else {
			slideIndex--;
		}

		if (slides.length < 10) {
			current.textContent = `0${slideIndex}`;
		} else {
			current.textContent = slideIndex;
		}

		setOpacity(dots);
	});

	dots.forEach(dot => {
		dot.addEventListener('click', (e) => {
			const slideTo = e.target.getAttribute('data-slide-to');

			slideIndex = slideTo;
			offset = deleteNotDigits(width) * (slideTo - 1);

			slidesField.style.transform = `translateX(-${offset}px)`;

			if (slides.length < 10) {
				current.textContent = `0${slideIndex}`;
			} else {
				current.textContent = slideIndex;
			}

			setOpacity(dots);
		});
	});

    // showSlides(slideIndex);

    // if (slides.length < 10) {
    //     total.textContent = `0${slides.length}`;
    // } else {
    //     total.textContent = slides.length;
    // }

    // function showSlides(n) {
    //     if (n > slides.length) {
    //         slideIndex = 1;
    //     }
    //     if (n < 1) {
    //         slideIndex = slides.length;
    //     }

    //     slides.forEach((item) => item.style.display = 'none');

    //     slides[slideIndex - 1].style.display = 'block'; // Как ваша самостоятельная работа - переписать на использование классов show/hide

    //     if (slides.length < 10) {
    //         current.textContent =  `0${slideIndex}`;
    //     } else {
    //         current.textContent =  slideIndex;
    //     }
    // }

    // function plusSlides (n) {
    //     showSlides(slideIndex += n);
    // }

    // prev.addEventListener('click', function(){
    //     plusSlides(-1);
    // });

    // next.addEventListener('click', function(){
    //     plusSlides(1);
    // });

	// My slider

	// const sliderItems = document.querySelectorAll('.offer__slide'),
	//     btnsArea = document.querySelector('.offer__slider-counter'),
	//     sliderNext = btnsArea.querySelector('.offer__slider-next'),
	//     sliderPrev = btnsArea.querySelector('.offer__slider-prev'),
	//     currSlide = btnsArea.querySelector(' #current'),
	//     totalSlides = btnsArea.querySelector(' #total');

	// let itemNum = 0;
	// totalSlides.innerHTML = getZero(sliderItems.length);

	// function switchItem(i = 0) {
	//     currSlide.innerHTML = `${getZero(i+1)}`;
	//     sliderItems.forEach((item, num) => {
	//         if (num === i) {
	//             item.classList.remove('hide');
	//             item.classList.add('show');
	//         } else {
	//             item.classList.remove('show');
	//             if (!item.classList.contains('hide')) {
	//                 item.classList.add('hide');
	//             }
	//         }
	//     });
	// }

	// switchItem(itemNum);

	// sliderNext.addEventListener('click', () => {
	//     itemNum++;
	//     if (itemNum >= sliderItems.length) {
	//         itemNum = sliderItems.length-1;
	//     }
	//     switchItem(itemNum);
	// });

	// sliderPrev.addEventListener('click', () => {
	//     itemNum--;
	//     if (itemNum < 0) {
	//         itemNum = 0;
	//     }
	//     switchItem(itemNum);
	// });

}
export default slider;